import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Router } from '@angular/router';
import { OpportunityService } from '../../opportunity.service';
import { forEach } from '@angular/router/src/utils/collection';
import { AgGridColumn } from 'ag-grid-angular';
import { UserService } from '../../../../common/user/user.service';
import { SelectListComponent } from "../SelectList/selectList.component";
import { SREditorComponent } from "../sr-editor/sr-editor.component";
import { SRRenderer } from "../sr-renderer/sr-renderer.component";
import { ActionRenderer } from "../action-renderer/action-renderer.component";
import { NumericCellComponent } from "../../../../common/controls/components/numericCell/numeric-cell.component";
import { DatePickerComponent } from "../../../../common/controls/components/datePickerCell/date-picker.component";
import { User } from "../../../../shared/models/user.model";
import { AuthService } from '../../../../shared/services/auth.service';
import { UserWorkFlow } from '../../../../shared/models/userworkflow.model';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import * as moment from 'moment';


@Component({
  selector: 'opportunity-list',
  templateUrl: './opportunityList.component.html',
  styleUrls: ['./opportunityList.component.css']
})

export class OpportunityListComponent {
  private params: any;
  private gridApi;
  private gridRowData;
  private gridColumnApi;
  private gridOptions;
  private getRowNodeId;

  public rowData: any[] = [];
  public rowsOldStateData: any[] = [];
  public rowsDeletedData: any[] = [];
  public lookUpData: any;
  public columnDefs: Array<any> = [];
  public defaultColDef = [];
  public rowClassRules;
  public editType;
  public user: User[];
  public frameworkComponents;
  public userWorkFlow: any;
  public template: string = undefined;

  constructor(private router: Router, private opportunityService: OpportunityService, private userService: UserService, private authService: AuthService, private spinnerService: Ng4LoadingSpinnerService) {

    this.rowClassRules = {
      "bg-in-progress": function (params) {
        var stat = params.data.status;
        return stat.toLowerCase() == "in progress";
      },
      "bg-won": function (params) {
        var stat = params.data.status;
        return stat.toLowerCase() == "won";
      },
      "bg-in-discussion": function (params) {
        var stat = params.data.status;
        return stat.toLowerCase() == "in discussion";
      },
      "bg-on-hold": function (params) {
        var stat = params.data.status;
        return stat.toLowerCase() == "on hold";
      },
      "bg-lost": function (params) {
        var stat = params.data.status;
        return stat.toLowerCase() == "lost";
      },

    };

    this.frameworkComponents = {
      selectList: SelectListComponent,
      sreditor: SREditorComponent,
      srRenderer: SRRenderer,
      actionRenderer: ActionRenderer,
      numericEditor: NumericCellComponent,
      datePickerEditor: DatePickerComponent

    };
    //this.editType = "fullRow";
    this.user = UserService.user;
    if (localStorage.getItem('userWorkFlow') != null) {
      this.userWorkFlow = JSON.parse(localStorage.getItem('userWorkFlow'));
    }
  }

  ngOnInit() {
    this.spinnerService.show();
    this.columnDefs = [];
    this.rowsOldStateData = [];
    this.getLookUpData();
    this.getOpportunities(true);
  }

  Logout() {
    localStorage.removeItem('userName');
    localStorage.removeItem('userToken');
    localStorage.removeItem('userWorkFlow')
    this.router.navigate(['/login']);
  }

  // Grid rows actions
  onUndo() {
    this.rowData = [];
    this.rowsOldStateData.forEach(element => {
      element['IsNewOrUpdated'] = false;
      this.rowData.push(Object.assign({}, element));
    });
    this.gridApi.updateRowData(this.rowData);
  }

  removeCurrentRow(gridAPI: any, selectedData: any) {
    var res = this.gridApi.updateRowData({ remove: selectedData });
  }

  onDeselectAll() {
    this.gridApi.deselectAll();
  }

  // Opportunities actions

  addNewOpportunity() {
    var opportunity = {};
    this.rowsOldStateData.forEach(function (data) {
      data.index++;
    });
    this.columnDefs.forEach(function (header) {
      opportunity[header.field] = '';
    });

    opportunity['IsNewOrUpdated'] = true;
    opportunity['isEditable'] = true;
    opportunity['IsAdded'] = true;
    opportunity['createdBy'] = this.userWorkFlow.email;
    opportunity['assignedToUserEmail'] = this.userWorkFlow.email;
    opportunity['assignedToUserName'] = this.userWorkFlow.firstName;
    var res = this.gridApi.updateRowData({ add: [opportunity], addIndex: 0 });
  }

  onRemoveOpportunity() {
    let deletedData = [];
    this.gridApi.getSelectedRows().forEach(row => {
      this.rowsDeletedData.push(row);
      deletedData.push(row);
    });
    var res = this.gridApi.updateRowData({ remove: deletedData });
  }

  saveOpportunity() {
    this.spinnerService.show();
    var rowData = [];

    this.gridApi.forEachNode((node) => {
      if (node.data['IsNewOrUpdated']) {
        let element = node.data;
        this.userWorkFlow.reportingManager.forEach(element => {
          if ((element.firstName + ' ' + element.lastName) == element['assignedToUserName']) {
            element['assignedToUserEmail'] = element.email;
            element['updatedBy'] = this.userWorkFlow.email;
          }
        });
        rowData.push(this.setOpportunityDefaultDataToSave(element, false));
      }
    });

    this.rowsDeletedData.forEach(element => {

      rowData.push(this.setOpportunityDefaultDataToSave(element, true));
    });

    this.opportunityService.SaveOpportunity(rowData).subscribe((data: any) => {
      this.getOpportunities(false);
    });
  }

  submitOpportunity(opportunity: any) {
    let isValid = true;
    for (var property in opportunity) {
      let currentField = opportunity[property];
      var currentColumdef = this.defaultColDef.find(function (element) {
        return element.name == property;
      });

      if (currentColumdef != undefined && currentColumdef.isRequired && (currentField == undefined || currentField == '')) {
        isValid = false;
        break;
      }
    }

    if (isValid) {
      this.spinnerService.show();
      var assignedToUserFirstName = opportunity['assignedToUserName'].substr(0, opportunity['assignedToUserName'].indexOf(' '));;
      var reportingManagers = this.userWorkFlow.reportingManager;

      console.log("assignedToUserFirstName:", assignedToUserFirstName);
      console.log("reposrtingManagers:", reportingManagers);

      opportunity['updatedBy'] = this.userWorkFlow.email;

      //If Assigned to Self
      if (assignedToUserFirstName.trim() === this.userWorkFlow.firstName.trim()) {
        opportunity['assignedToUserEmail'] = this.userWorkFlow.email;
      }
      else {
        opportunity['assignedToUserEmail'] = reportingManagers.find(x => x.firstName.trim() == assignedToUserFirstName.trim()).email;
        console.log("assignedToUserEmail", opportunity['assignedToUserEmail']);
      }

      this.opportunityService.SubmitOpportunity(opportunity).subscribe((data: any) => {
        this.spinnerService.hide();
        alert("Opportunity Submitted Successfully!");
        this.getOpportunities(false);
      });

    } else {
      alert('Please fill all required field/ fields');
    }
  }

  setOpportunityDefaultDataToSave(opportunity: any, isDeleted: boolean) {
    for (var property in opportunity) {
      let currentField = opportunity[property];
      var currentColumdef = this.defaultColDef.find(function (element) {
        return element.name == property;
      });

      if (currentColumdef != undefined && currentColumdef.type == 5) {
        opportunity[property] = this.getSaveDatewithFormat(currentField);
      }
    }

    opportunity['updatedBy'] = this.userWorkFlow.email;
    if (isDeleted) {
      opportunity['isActive'] = false;
    }

    return opportunity;
  }

  // Grid data initialisation
  initialiseDynamicGridHeaders(headers: Array<any>) {
    headers.forEach(element => {
      if (element.isDisplay) {
        switch (element.type) {
          case 2:
            this.columnDefs.push(this.getDropDownColumn(element));
            break;
          case 3:
            this.columnDefs.push(this.getPopupColumn(element));
            break;
          case 4:
            this.columnDefs.push(this.getNumericColumn(element));
            break;
          case 5:
            this.columnDefs.push(this.getDatePickerColumn(element));
            break;
          case 6:
            this.columnDefs.push(this.getRichTextEditorColumn(element));
            break;
          default:
            this.columnDefs.push(this.getDefaultColumn(element));
            break;
        }
      }
    });
  }

  initialiseGridRows(rows: Array<any>) {
    this.rowData = [];
    rows.forEach(element => {
      for (var property in element) {
        let currentField = element[property];
        var currentColumdef = this.defaultColDef.find((field) => {
          return field.name == property;
        });

        if (currentColumdef != undefined && currentColumdef.type == 5) {
          element[property] = this.getDisplayDatewithFormat(currentField);
        }
      }

      element['IsNewOrUpdated'] = false;
      if (this.userWorkFlow.reportingManager != undefined) {
        this.userWorkFlow.reportingManager.forEach(element => {
          if ((element.firstName + ' ' + element.lastName) == element['assignedToUserName']) {
            element['assignedToUserEmail'] = element.email;
          }
        });
      }
      this.rowData.push(element);
      this.rowsOldStateData.push(Object.assign({}, element));
      var res = this.gridApi.setRowData(this.rowData);
    });
  }



  // To filter Opportunities Waiting for Logged In User Approval
  filterMyPendingApprovals() {
    var data = this.rowData;
    this.rowData = [];
    data.forEach(element => {
      element['IsNewOrUpdated'] = false;
      if (element.assignedToUserEmail == this.userWorkFlow.email && element.approvedStatus == "Pending") {
        this.rowData.push(
          element
        );
      }
    });
  }

  getOpportunities(isFirstTime: boolean) {
    // Enable loader
    this.spinnerService.show();
    this.opportunityService.getOpportunityData().subscribe((data: any) => {
      if (isFirstTime) {
        this.defaultColDef = data.headers;
        this.rowData = [];
        this.columnDefs = [];

        // Create Static Columns
        this.columnDefs.push(this.getCheckBoxColumn());
        this.columnDefs.push(this.getActionColumn());
        this.columnDefs.push(this.getIdentityColumn());

        // Create Dynamic Columns

        this.initialiseDynamicGridHeaders(data.headers);
      }

      this.initialiseGridRows(data.rows);

      // Disable Loader
      this.spinnerService.hide();
    }, err => {
      this.spinnerService.hide();
    });
  }

  getLookUpData() {   
    this.opportunityService.getLookUpData().subscribe((data: any) => {
      this.lookUpData = data;
    }, err => {
      this.spinnerService.hide();
    });
  }

  getSaveDatewithFormat(value: any) {
    if (value != undefined) {
      return moment(value, 'DD/MM/YYYY').format('YYYY-MM-DD HH:mm:ss');
    }

    return undefined;
  }

  getDisplayDatewithFormat(value: any) {
    if (value != undefined) {
      let validDate: Date = new Date(value);
      return moment(validDate).format('DD/MM/YYYY');
    }

    return undefined;
  }

  getSpecificLookUpData(lookUpType) {
    let rmNameList: any = [];
    if (lookUpType === 'AssignedToUserName') {

      this.userWorkFlow.reportingManager.forEach(element => {
        rmNameList.push(element.firstName + ' ' + element.lastName);
      });
      return { values: rmNameList };
    }
    let lookUpObjects = this.lookUpData.filter(obj => {
      return obj.lookUpType === lookUpType
    })

    let lookUpData = lookUpObjects.map(function (item) {
      return item['value'];
    });
    lookUpData.forEach(item => {
      rmNameList.push(item);
    });
    return { values: rmNameList };
  }

  getCheckBoxColumn() {

    return {
      headerName: '#',
      width: 30,
      checkboxSelection: true,
      suppressSorting: true,
      suppressMenu: true,
      pinned: true,
      suppressFilter: true
    };
  }

  getActionColumn() {
    return {
      headerName: "Actions",

      field: "id",
      editable: false,
      width: 90,
      suppressFilter: true,
      pinned: true,
      cellRenderer: "actionRenderer",
      cellRendererParams: {
        userWorkFlow: this.userWorkFlow,
        rowsOldStateData: this.rowsOldStateData,
        deleteRow: (rowIndex: any) => {
          var selectedData = this.gridApi.getRowNode(rowIndex);
          let deletedData = [];
          deletedData.push(selectedData.data);
          this.rowsDeletedData.push(selectedData.data);
          var res = this.gridApi.updateRowData({ remove: deletedData });
        },
        undoRow: (rowIndex: any) => {
        },
        submitRow: (rowIndex: any) => {
          var selectedData = this.gridApi.getRowNode(rowIndex);
          this.submitOpportunity(selectedData.data);
        },
      },
      cellEditorParams: function (params) {
        return params.value;
      }
    };
  }

  getIdentityColumn() {
    return {
      headerName: "ID",
      field: "id",
      editable: false,
      width: 60
    };
  }

  getDropDownColumn(column: any) {

    return {
      headerName: column.title,
      field: column.name,
      editable: !column.isReadOnly,
      cellRenderer: "selectList",
      cellEditor: 'agSelectCellEditor',
      cellEditorParams: (params) => {
        return this.getSpecificLookUpData(column.lookUpType);
      },
      headerTooltip: column.title,
      width: 100

    }
  }

  getPopupColumn(column: any) {

    return {
      headerName: column.title,
      field: column.name,
      editable: !column.isReadOnly,
      cellRenderer: "srRenderer",
      cellEditor: 'sreditor',
      headerTooltip: column.title,
      width: 100
    };
  }

  getNumericColumn(column: any) {
    return {
      headerName: column.title,
      field: column.name,
      editable: !column.isReadOnly,
      cellEditor: "numericEditor",
      headerTooltip: column.title,
      width: 100
    };
  }

  getDatePickerColumn(column: any) {
    return {
      headerName: column.title,
      field: column.name,
      editable: !column.isReadOnly,
      cellEditor: "datePickerEditor",
      cellRendererParams: {

      },
      cellEditorParams: (params: any) => {
        return params.value;
      },
      headerTooltip: column.title,
      width: 100
    };

  }

  getRichTextEditorColumn(column: any) {
    return {
      headerName: column.title,
      field: column.name,
      editable: !column.isReadOnly,
      cellEditor: 'agLargeTextCellEditor',
      cellEditorParams: {
        maxLength: '300',
        cols: '50',
        rows: '6'
      },
      width: 100,
      headerTooltip: column.title,
      tooltip: (params: any) => {
        return params.value;
      }
    }
  }

  getDefaultColumn(column: any) {
    return {
      headerName: column.title,
      field: column.name,
      editable: !column.isReadOnly,
      width: 100,
      headerTooltip: column.title,
      tooltip: (params: any) => {
        return params.value;
      }
    };
  }

  //Grid Functions ----Start

  onRowEditing(row: any) {
  }

  rowClick(param) {
  }

  onCellValueChanged(row: any) {
    row.data['IsNewOrUpdated'] = true;
    var params = {
      force: true,
      rowNodes: row.node.RowNode
    };
    this.gridApi.refreshCells(params);
  }

  changeEdit(row: any) {
    row.isEditable = true;
  }

  changeSave(row: any) {
    row.isEditable = false;
  }

  onGridReady(params) {
    this.gridRowData = params.rowData;
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
  }

  sizeToFit() {
    this.gridApi.sizeColumnsToFit();
  }

  autoSizeAll() {
    var allColumnIds = [];
    this.gridColumnApi.getAllColumns().forEach(function (column) {
      allColumnIds.push(column.colId);
    });
    this.gridColumnApi.autoSizeColumns(allColumnIds);
  }

  exportToExcel() {
    var params = {
      fileName: "Test",
      sheetName: "TestSheet"
    };

    //this.gridApi.exportDataAsExcel();
    this.gridApi.exportDataAsCsv();
  }

  onModelUpdated() {
  }

  //Grid Functions ----End
}




