import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OpportunityListComponent } from './components/opportunityList/opportunityList.component';
import { AuthGuard } from '../../shared/services/auth.guard.service';

const opportunityRoutes: Routes = [
  {
    path: '',
    component: OpportunityListComponent,
    canActivate: [AuthGuard],
    children: [

      {
        path: '',
        children: [
          { path: 'opportunitylist', component: OpportunityListComponent, canActivate: [AuthGuard] }
          //{
          //  path: '',
          //  canActivateChild: [AuthGuard],
          //  component: SelfSpeechComponent
          //},
          //{ path: 'allspeech', component: AllSpeechComponent },
          //{ path: 'filterspeech', component: SearchResultSpeechComponent },
          //{ path: 'newspeech', component: NewSpeechComponent, canDeactivate: [CanDeactivateService] },
          //{ path: 'userspeech', component: SelfSpeechComponent }
        ]
      }
    ]
  }

];

export const OpportunityRouting: ModuleWithProviders = RouterModule.forChild(opportunityRoutes);



