import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import { ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { OpportunityRouting } from './opportunity.routing';
import { SharedModule } from '../../shared/shared.module';

import { OpportunityService } from './opportunity.service';
import { SRService } from './sr.service';

// ag-grid
import { AgGridModule } from 'ag-grid-angular';
import { OpportunityListComponent } from './components/opportunityList/opportunityList.component';
import { SelectListComponent } from "./components/SelectList/selectList.component";
import { SREditorComponent } from "./components/sr-editor/sr-editor.component";
import { SRRenderer } from "./components/sr-renderer/sr-renderer.component";
import { ActionRenderer } from "./components/action-renderer/action-renderer.component";

@NgModule({
  imports: [
    //BrowserModule,
    CommonModule,
    FormsModule,
    SharedModule,
    RouterModule,
    OpportunityRouting,
    ReactiveFormsModule,
    Ng4LoadingSpinnerModule.forRoot(),
    AgGridModule.withComponents([SelectListComponent, SREditorComponent, SRRenderer, ActionRenderer])
  ],
  declarations: [OpportunityListComponent, SelectListComponent, SREditorComponent, SRRenderer, ActionRenderer],
  exports: [OpportunityListComponent],
  entryComponents: [OpportunityListComponent],
  providers: [OpportunityService, SRService]

})
export class OpportunityModule { }



