import { AfterViewInit, Component, OnInit, TemplateRef, ViewChild, ViewContainerRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Router } from '@angular/router';
import { SRService } from '../../sr.service';
import { forEach } from '@angular/router/src/utils/collection';
import { AgGridColumn } from 'ag-grid-angular';

import {ICellEditorAngularComp} from "ag-grid-angular";

@Component({
	selector: 'editor-cell',
	templateUrl: './sr-editor.component.html',
	styleUrls: ['./sr-editor.component.css']
})

export class SREditorComponent implements ICellEditorAngularComp, AfterViewInit {
	private params: any;
	private gridApi;
	private gridColumnApi;
	private gridOptions;
  public rowSelection;
	public rowData: any[];
	public columnDefs;
	public defaultColDef = [];
	public rowClassRules;
	public frameworkComponents;
	public editType;
	public srID: any;
	public isSelected: boolean;

    @ViewChild('container', { read: ViewContainerRef }) public container;

	//public happy: boolean = false;

	   ngAfterViewInit() {
        setTimeout(() => {
            this.container.element.nativeElement.focus();
        })
    }

	   agInit(params: any): void {
		this.params = params;
		this.srID = params.node.data["sr"];
		////To Select the Row with RowID in the grid
		//console.log("CEllEditor Node:", params.node);
		//console.log("CEllEditor Data:", params.node.data);
		//console.log("CEllEditor Selected Cell SR:", this.srID);
	}

	   getValue(): any {
		//this.params.api.stopEditing();
		return this.srID;
	   }

	   isPopup(): boolean {
		return true;
	   }

	   constructor(private router: Router, private srService: SRService) {
		this.rowSelection = 'single';
	}

	ngOnInit() {
		this.getSRList();
	}
	//Grid Functions ----Start
	onGridReady(params) {
		this.gridApi = params.api;
		this.gridColumnApi = params.columnApi;
	}
	//To get SR List
	getSRList() {
		this.srService.getSRData().subscribe((data: any) => {
			//console.log(data);
			this.columnDefs = [
				{
					headerName: "ID",
					field: "id",
					width: 130
				},
			];

			this.rowData = [

			];
			data.headers.forEach(element => {
				this.columnDefs.push(
					{
						headerName: element.title,
						field: element.name,
						width: 130
					}
				);
			});

			data.rows.forEach(element => {
				element['IsNewOrUpdated'] = false;
				this.rowData.push(
					element
				);
			});

		});
	}
	//To Select the SR Data

	onSelectionChanged() {
		//console.log("On Selection Changed");
		var selectedRows = this.gridApi.getSelectedRows();
		var selectedRowsString = "";
		selectedRows.forEach(function (selectedRow, index) {
			if (index !== 0) {
				selectedRowsString += ", ";
			}
			selectedRowsString += selectedRow.id;
		});
		//console.log("selectedRowsString:", selectedRowsString);
		this.srID = selectedRowsString;
	}

	onOK() {
		this.isSelected = true;
		this.onSelectionChanged();
		this.params.stopEditing();
		//alert("OK Button Clicked!");
	}

	onCancel() {
		this.isSelected = false;
		this.params.stopEditing();
	}
	//Grid Functions ----End
}




