import {Component} from "@angular/core";
import {ICellRendererAngularComp} from "ag-grid-angular";


@Component({
	selector: 'sr-cell',
	templateUrl: './sr-renderer.component.html',
	styleUrls: ['./sr-renderer.component.css']
})

export class SRRenderer implements ICellRendererAngularComp {

	private params: any;
	public srID: any;

	agInit(params: any): void {
		this.params = params;
		this.setSR(params);
	}

	refresh(params: any): boolean {
		this.params = params;
		this.setSR(params);
		return true;
	}

	public setSR(params) {
		if (params.value == null) {
			this.srID = params.data.sr;
		}
		else {
			this.srID = params.value;
		}

	}
}




